<template>
  <div>
    <!-- <button @click="getBonos()" class="btn my-3 btn-secondary">
      BUSCAR
    </button>
    <button type="button" @click="start()" class="btn my-3 ml-2 btn-secondary">Start</button> 
    <button type="button" @click="fin()" class="btn my-3 ml-2 btn-secondary">Stop</button>  -->
    <div class="row">
      <div class="m-auto mb-1 justify-content-center col-3">
        <label for="">Dia: </label>
        <select class="form-control" name="" v-model="filtro">
          <option v-for="item in bonos" :key=item.uid :value="item.uid">{{item.uid}}</option>
        </select>
        <label for="">AL30 - GD30</label> <br>
      </div>
      <div  class="m-auto mb-1 justify-content-center col-3">
            <table id="Tabla1" class="">
              <thead>
                <tr>
                  <td style="width: 10%"></td>
                  <td style="width: 10%">Monto:</td>
                  <td style="width: 10%"><input style="width: 100%" type="text"  v-model="r2dolares" name="" id=""></td>
                  <td> <button class="btn btn-success" style="padding:0" @click="calcularR2()">Calcular</button> </td>
                </tr>
                  <tr>
                    <th scope="col"></th>
                    <th style="border: 1px solid black; background: darkgray;" scope="col">Cantidad</th>
                    <th style="border: 1px solid black; background: darkgray;" scope="col">Precio</th>
                    <th style="border: 1px solid black; background: darkgray;" scope="col">U$S</th>
                    <th style="border: 1px solid black; background: darkgray;" scope="col">Cable</th>
                    <th style="border: 1px solid black; background: darkgray;" scope="col">0,01%</th>
                  </tr>
              </thead>
              <tbody>
                <tr>
                  <td ></td>
                  <td style="border: 1px solid black;" >{{r2CantidadComprada1}}</td>
                  <td style="width: 10%"><input style="width: 100%" type="text"  v-model="r2pCompra1" name="" id=""></td>
                  <!-- <td style="border: 1px solid black;" >{{r2pCompra1.toFixed(2)}}</td> -->
                  <td style="border: 1px solid black;" >{{(r2CantidadComprada1*r2pCompra1/100).toFixed(2)}}</td>
                  <td style="border: 1px solid black;" ></td>
                  <td style="border: 1px solid black;" >{{(r2comision1).toFixed(2)}}</td>
                  <td ></td>
                </tr>
                <tr>
                  <td></td>
                  <td style="border: 1px solid black;" >{{r2CantidadComprada1}}</td>
                  <td style="width: 10%"><input style="width: 100%" type="text"  v-model="r2pVenta1" name="" id=""></td>
                  <!-- <td style="border: 1px solid black;" >{{r2pVenta1.toFixed(2)}}</td> -->
                  <td style="border: 1px solid black;" ></td>
                  <td style="border: 1px solid black;" >{{r2fin1.toFixed(2)}}</td>
                  <td style="border: 1px solid black;" >{{r2comision2.toFixed(2)}}</td>
                  <td ></td>
                </tr>
                <tr>
                  <td></td>
                  <td style="border: 1px solid black;" >{{r2CantidadComprada2}}</td>
                  <td style="width: 10%"><input style="width: 100%" type="text"  v-model="r2pCompra2" name="" id=""></td>
                  <!-- <td style="border: 1px solid black;" >{{r2pCompra2.toFixed(2)}}</td> -->
                  <td style="border: 1px solid black;" ></td>
                  <td style="border: 1px solid black;" >{{(r2CantidadComprada2*r2pCompra2/100).toFixed(2)}}</td>
                  <td style="border: 1px solid black;" >{{r2comision3.toFixed(2)}}</td>
                  <td ></td>
                </tr>
                <tr>
                  <td></td>
                  <td style="border: 1px solid black;" >{{r2CantidadComprada2}}</td>
                  <td style="width: 10%"><input style="width: 100%" type="text"  v-model="r2pVenta2" name="" id=""></td>
                  <!-- <td style="border: 1px solid black;" >{{r2pVenta2.toFixed(2)}}</td> -->
                  <td style="border: 1px solid black;" >{{r2fin2.toFixed(2)}}</td>
                  <td style="border: 1px solid black;" ></td>
                  <td style="border: 1px solid black;" >{{r2comision4.toFixed(2)}}</td>
                  <td ></td>
                </tr>
                <tr>
                  <td></td>
                  <td style="border: 1px solid black; background: orange;" >{{(r2pCompra1-r2pVenta1).toFixed(2)}}</td>
                  <td style="border: 1px solid black; background: orange;" >{{(r2pVenta2-r2pCompra2).toFixed(2)}}</td>
                  <td style="border: 1px solid black;" >{{(r2fin2-(r2CantidadComprada1*r2pCompra1/100)).toFixed(2)}}</td>
                  <td style="border: 1px solid black;" >x Rulo</td>
                  <td style="border: 1px solid black;" >{{(r2comision1+r2comision2+r2comision3+r2comision4).toFixed(2)}}</td>
                  <td ></td>
                </tr> 
                
                <tr>
                  <td></td>
                  <td style="border: 1px solid black; background: orange;" colspan="2">{{((r2pVenta2-r2pCompra2)-(r2pCompra1-r2pVenta1)).toFixed(2)}}</td>
                  <td style="border: 1px solid black;" >{{(((r2comision1+r2comision2+r2comision3+r2comision4)*-1)).toFixed(2)}}</td>
                  <td style="border: 1px solid black;" >x Com.</td>
                  <td style="border: 1px solid black;" >{{((r2comision1+r2comision2+r2comision3+r2comision4)).toFixed(2)}}</td>
                  <td ></td>
                </tr>
                <tr>
                  <td></td>
                  <td style="border: 1px solid black; background: green; color: white;">Rdo</td>
                  <td style="border: 1px solid black; background: green; color: white;" colspan="4">{{(r2fin2-(r2CantidadComprada1*r2pCompra1/100)+(r2comision1+r2comision2+r2comision3+r2comision4)).toFixed(2)}}</td>
                </tr>
              </tbody>
              </table>
      </div>
    </div>

    <div class="justify-content-center">
      <div>
            <button type="button" @click="exportTableToExcel('Tabla','Datos Bonos AL30-GD30')" class="btn my-3 ml-2 btn-success">Excel</button>
        </div>
    </div>
    <div class="justify-content-center">
      <table id="Tabla" class="table table-sm col-12" v-if="filtro != ''">
        <thead class="thead-dark">
          <tr>
            <th scope="col">Desde</th>
            <th scope="col">Hasta</th>
            <th scope="col">Dif1</th>
            <th scope="col">Dif2</th>
            <th scope="col">Ratio</th>
            <th scope="col">AL30D Q</th>
            <th scope="col">AL30C Q</th>
            <th scope="col">GD30C Q</th>
            <th scope="col">GD30D Q</th>
            <th scope="col">AL30D P</th>
            <th scope="col">AL30C P</th>
            <th scope="col">GD30C P</th>
            <th scope="col">GD30D P</th>
            <th scope="col">Monto</th>
            <th scope="col">Q AL30</th>
            <th scope="col">Q GD30</th>
            <th scope="col">Dolares</th>
            <th scope="col">Cable</th>
            <th scope="col">Com</th>
            <th scope="col">Gcias</th>
            <th scope="col">%</th>
            <th scope="col">Hora</th>
            <th scope="col">Accion</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in bonos.find(e => e.uid == filtro).AL30GD30" :key=item.uid v-bind:class="item.ratio >= 0.18 && item.al30d_q >= item.q_al30 ? 'table-success' :item.restoD+item.comision > 0 ? 'table-warning' :''">
            <td> {{item.desde}} </td>
            <td> {{item.hasta}} </td>
            <td> {{item.dif1.toFixed(2)}} </td>
            <td> {{item.dif2.toFixed(2)}} </td>
            <td style="font-weight: bold;"> {{item.ratio.toFixed(2)}} </td>
            <td> {{item.al30d_q}} </td>
            <td> {{item.al30c_q}} </td>
            <td> {{item.gd30c_q}} </td>
            <td> {{item.gd30d_q}} </td>
            <td> {{item.al30d_p.toFixed(2)}} </td>
            <td> {{item.al30c_p.toFixed(2)}} </td>
            <td> {{item.gd30c_p.toFixed(2)}} </td>
            <td> {{item.gd30d_p.toFixed(2)}} </td>
            <td> {{item.monto}} </td>
            <td> {{item.q_al30.toFixed(0)}} </td>
            <td> {{item.q_gd30.toFixed(0)}} </td>
            <td> {{item.restoD.toFixed(2)}} </td>
            <td> {{item.restoC.toFixed(2)}} </td>
            <td> {{item.comision.toFixed(2)}} </td>
            <td style="font-weight: bold;"> {{(item.restoD+item.comision).toFixed(2)}} </td>
            <td style="font-weight: bold;"> {{(item.restoD/item.monto*100).toFixed(2)}} </td>
            <td> {{
              ((item.dia.toDate().getHours()).toString().length == 1 ? '0'+item.dia.toDate().getHours() : item.dia.toDate().getHours())
              +':'+
              ((item.dia.toDate().getMinutes()).toString().length == 1 ? '0'+item.dia.toDate().getMinutes() : item.dia.toDate().getMinutes())
              +':'+
              ((item.dia.toDate().getSeconds()).toString().length == 1 ? '0'+item.dia.toDate().getSeconds() : item.dia.toDate().getSeconds())
              }} </td>
            <!-- item.dia.getHours()+':'+item.dia.getMinutes()+':'+item.dia.getSeconds() -->
            <td> <button class="btn btn-success" style="padding:0" @click="seleccionar(item)">Seleccionar</button> </td>
          </tr>
        </tbody>
      </table>
    </div>

  </div> 
</template>



<script>

import { mapState, mapActions , mapGetters} from "vuex";


export default {
  name: 'bonos',
  data(){
    return {
      pantallaVentas: false,
      pantallaImagenes: false,
      pantallaBanners: false,
      latitud:'',
      longitud:'' ,
      filtro:'',
      timer:'',
      r2dolares : 2000,
      r2comision1: 0,
      r2comision2: 0,
      r2comision3: 0,
      r2comision4: 0,
      r2CantidadComprada1 : 0,
      r2CantidadComprada2 : 0,
      r2fin1: 0,
      r2fin2: 0,
      r2pCompra1:0,
      r2pCompra2:0,
      r2pVenta1:0,
      r2pVenta2:0,

    }
  },
  methods: {
    ...mapActions(['getBonos','getBonos']),
    start(){
      this.timer = setInterval( ()=>{
        this.getBonos()
        // this.buscar();
        // setTimeout(()=>{ 
        //   this.getBonos();
        //   }, 3000);
        // console.log(new Date);
        },3000)
    },
    fin(){
      clearInterval(this.timer);
    },
    seleccionar(item){

      this.r2pCompra1 = item.al30d_p
      this.r2pCompra2 = item.gd30c_p
      this.r2pVenta1 = item.al30c_p
      this.r2pVenta2 = item.gd30d_p

    },
    calcularR2(){

        this.comision = 0.00
        this.mercado = 0.01

        // this.r2pCompra1 = this.comparacion.find(l => l.nombre == this.rulo2e).pVentaDolar
        this.r2CantidadComprada1 = Math.trunc((this.r2dolares) / this.r2pCompra1 * 100)
        this.r2comision1 = ((this.r2pCompra1 * this.r2CantidadComprada1)/100 * (this.comision + this.mercado) / 100)*-1

        // this.r2pVenta1 = this.comparacion.find(l => l.nombre == this.rulo2e).pCompraCable
        this.r2fin1 = this.r2CantidadComprada1 *  this.r2pVenta1 /100
        this.r2comision2 =  (this.r2fin1 * (this.comision + this.mercado) / 100)*-1

        // this.r2pCompra2 = this.comparacion.find(l => l.nombre == this.rulo2s).pVentaCable
        this.r2CantidadComprada2 = Math.trunc((this.r2fin1) / this.r2pCompra2 * 100)
        this.r2comision3 = ((this.r2pCompra2 * this.r2CantidadComprada2)/100 * (this.comision + this.mercado) / 100)*-1

        // this.r2pVenta2 = this.comparacion.find(l => l.nombre == this.rulo2s).pCompraDolar
        this.r2fin2 = this.r2CantidadComprada2 *  this.r2pVenta2 /100
        this.r2comision4 =  (this.r2fin2 * (this.comision + this.mercado) / 100)*-1

      },
    buscar(){
        this.axios.get('/token')
        .then(res => {
          console.log(res);
        })
        .catch(error => {
          console.log(error.response);
        })
    },
    exportTableToExcel(tableID, filename){
    var downloadLink;
    var dataType = 'application/vnd.ms-excel';
    var tableSelect = document.getElementById(tableID);
    var tableHTML = tableSelect.outerHTML.replace(/ /g, '%20');
    
    // Specify file name
    filename = filename?filename+'.xls':'excel_data.xls';
    
    // Create download link element
    downloadLink = document.createElement("a");
    
    document.body.appendChild(downloadLink);
    
    if(navigator.msSaveOrOpenBlob){
        var blob = new Blob(['ufeff', tableHTML], {
            type: dataType
        });
        navigator.msSaveOrOpenBlob( blob, filename);
    }else{
        // Create a link to the file
        downloadLink.href = 'data:' + dataType + ', ' + tableHTML;
    
        // Setting the file name
        downloadLink.download = filename;
        
        //triggering the function
        downloadLink.click();
    }
    },

  },
  computed: {
    ...mapState(['productos','usuario','bonos'])
  },
  created() {
  },
  mounted() {
    // this.getProductos()
    $('body').css('overflow-y','scroll')
    this.getBonos()
    // this.getBonos48()
  }
}
</script>
